import React from 'react';
import { useNavigate } from 'react-router-dom';
import Questionnaire, {Question} from '../../Questionnaire';

const questions: Question[] = [
    {
        question: 'Which data structure is ordered and changeable?',
        options: ['List', 'Dictionary', 'Set', 'Tuple'],
        answer: 'List',
        explanation: {
            'List': 'Correct. A list is ordered and changeable.',
            'Dictionary': 'Incorrect. A dictionary is unordered and changeable.',
            'Set': 'Incorrect. A set is unordered and unindexed.',
            'Tuple': 'Incorrect. A tuple is ordered but unchangeable.'
        }
    },
    {
        question: 'How do you access the value associated with the key "Earth" in a dictionary called planet_moons?',
        options: ['planet_moons["Earth"]', 'planet_moons.Earth', 'planet_moons->Earth', 'planet_moons{Earth}'],
        answer: 'planet_moons["Earth"]',
        explanation: {
            'planet_moons["Earth"]': 'Correct. This is the correct syntax to access the value associated with the key "Earth" in a dictionary.',
            'planet_moons.Earth': 'Incorrect. This syntax is not valid for accessing dictionary values in Python.',
            'planet_moons->Earth': 'Incorrect. This is not valid Python syntax.',
            'planet_moons{Earth}': 'Incorrect. This is not valid Python syntax.'
        }
    },
    {
        question: 'Which of the following is a key-value pair?',
        options: ['[1, 2, 3]', '("Mercury", 0)', '{ "Mercury": 0 }', '{1, 2, 3}'],
        answer: '{ "Mercury": 0 }',
        explanation: {
            '[1, 2, 3]': 'Incorrect. This is a list of values.',
            '("Mercury", 0)': 'Incorrect. This is a tuple.',
            '{ "Mercury": 0 }': 'Correct. This is a key-value pair in a dictionary.',
            '{1, 2, 3}': 'Incorrect. This is a set.'
        }
    },
    {
        question: 'How do you add an item to a list called planets?',
        options: ['planets.add("Pluto")', 'planets.append("Pluto")', 'planets.insert("Pluto")', 'planets["Pluto"] = True'],
        answer: 'planets.append("Pluto")',
        explanation: {
            'planets.add("Pluto")': 'Incorrect. The add method is used for sets, not lists.',
            'planets.append("Pluto")': 'Correct. The append method is used to add an item to the end of a list.',
            'planets.insert("Pluto")': 'Incorrect. The insert method requires an index position as well.',
            'planets["Pluto"] = True': 'Incorrect. This syntax is used for adding key-value pairs to dictionaries.'
        }
    },
];

const Lecture3Questionnaire: React.FC = () => {
    const navigate = useNavigate();

    const handleSuccess = () => {
        const completionStatus = JSON.parse(localStorage.getItem('completionStatus') || '{}');
        completionStatus['lecture3Questionnaire'] = true;
        localStorage.setItem('completionStatus', JSON.stringify(completionStatus));
        navigate('/week1');
    };

    return <Questionnaire questions={questions} onSuccess={handleSuccess} />;
};

export default Lecture3Questionnaire;
